
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
body {
  margin: 0;
  color: #e4d2d8;
  padding-top: 8%;
}

.site-header {
    top: 0;
    left: 0;
    display: block;
    color: #e4d2d8;
    font-weight: bold;
    background-color: #d69090;
    height: 8%;
    width: 100%;
    position: fixed;
}
.site-header-wrapper {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    height: 100%;
    margin: 0 5%;
}
.site-logo {
    width: 40%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: start;
        -ms-flex-pack: start;
            justify-content: start;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    color: white;
    text-decoration: none;
}
h1 {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    font-size:  x-large;
    font-family: sans-serif;
    margin: 0;
    margin-left: 8px;
}
.logo-img {
    height: 80%;
    border-radius: 4px;
}
.nav {
    width: 80%;
    text-align: right;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: end;
        -ms-flex-pack: end;
            justify-content: end;
}
ul {
    list-style: none;
}
.header-li {
    height: 100%;
    display: inline-block;
    font-size: large;
    margin-left: 8px;
}
.menu-item {
    height: 100%;
    color: white;
    text-decoration: none;
}

















